.restaurantCard {
    display: -ms-flexbox;
    display: flex;
    height: 300px;
    width: 680px;
    margin: 0 auto;
    margin-bottom: 20px;
    padding: 15px;
    border: 2px solid darkgrey;
    -ms-flex-pack: start;
        justify-content: flex-start;
    -ms-flex-align: center;
        align-items: center;
    background-color: lightgray;
}

.restaurantPics {
    height: 225px;
    width: 320px;
}

.restaurantInfo {
    text-align: left;
    margin: 35px;
}

.favBtn {
    background-color: #6983B2;
    color: white;
    height: 40px;
    width: 125px;
}

.favBtn:hover {
    background-color: tomato;
    -webkit-transition: .40s;
    -o-transition: .40s;
    transition: .40s;
}


